*{
	box-sizing: border-box;
	&:before, &:after{
		box-sizing: border-box;
	}
}
:root{ font-size: #{$root-fontSize}px }

body{
	font-family: $root-fontFamily;
	font-weight: $root-fontWeight;
	line-height: $root-lineHeight;
	margin: 0;
	color: $body-color;
	word-break: break-word;
	-webkit-font-smoothing: antialiased;
	background-color: white;
}

h1,h2,h3,h4,h5,h6{
	color       : $heading-color;
	line-height : normal;
	font-family : $heading-font;
	font-weight : 700;
}

a{
	text-decoration: none;
	color: inherit;
	transition: .3s ease;
}

figure, img, iframe{
	max-width: 100%;
}

figure{
	margin: 0;
}

.screen-reader-text{
	position: absolute;
	width: 1px;
	height: 1px;
	visibility: hidden;
	overflow: hidden;


}

.site-content{
	@include clearfix;
	overflow-x: hidden;
	// display: flex;
	// flex-direction: column;
	min-height: 100vh;
}

.main-content{
	min-height: 50vh;
}

.white-space{
	border: none;
	height: 50px;
}

blockquote{
	font-family: $decoration-font;
	p{
		font-family: inherit;
	}
}

.separator{
	border: none;
	border-top: 1px solid rgba(0,0,0,.1);
}

.list-unstyled{
	list-style: none;
	padding: 0;
}
.list-inline{
	padding: 0;
	li{
		display: inline-block;
	}
}