.post-type-archive-product{
	.main-content .row >.content-area{
		padding: 0 15px;
	}
}
//- Product Grid View
.product-grid{
	.product{
		margin-bottom: 50px;
		@media screen and (max-width: 480px){
			margin-bottom: 30px;
		}
		&:hover{
			.product__action{ opacity: 1 }
		}
		&__image{
			position: relative;
			transition: transform .3s ease;
			> a{ 
				overflow: hidden;
				display: block;
			}
			img{
				max-width : 100%;
				display   : block;
				width: 100%;
				height: auto;
				transition: .3s ease;
			}
		}
		
		.onsale{
			position         : absolute;
			right            : -10px;
			top              : 20px;
			background-color : #4f75f2;
			color            : white;
			line-height      : 1;
			padding          : 8px 10px;
			z-index          : 2;
			@include em(14)
		}
		&__detail{
			padding: 20px;
			padding-bottom: 0;
			z-index: 1;
			position: relative;
			text-align: center;
			> .yith-wcqv-button{ display: none}
		}
		&__title{
			@include em(16);
			margin      : 0 0 10px;
			line-height : 1.3;
			white-space: nowrap;
			overflow: hidden;
			text-overflow: ellipsis;
			
			@media screen and (max-width: 480px){
				@include em(16);
			}
			
			a{ 
				color           : inherit;
				text-decoration : none;
			}
		}
		&__category{
			@include em(12);
			white-space: nowrap;
			overflow: hidden;
			margin-bottom: 10px;
			text-overflow: ellipsis;
		}
		&__action{
			padding: 20px;
			padding-top: 0;
		}
		.author{
			font-style  : italic;
			color       : $detail-color;
			@include em(14);
		}
		&__price{
			color: $heading-color;
			@include em(16);

			@media screen and (max-width: 480px){
				@include em(14);
			}
		}
		.star-rating{
			text-align: left;
			display: inline-block;
		}
		.add_to_cart_button,
		.ajax_add_to_cart,
		.product_type_variable,
		.product_type_external{
			background-color: #d8dcec;
			box-shadow: none;
			position: relative;
			margin: 5px 0;
			// border-radius: 50%;
			// display: flex;
			// align-items: center;
			// justify-content: center;
			color: white;

			.mdi{
				margin-right: 15px;
				display: inline-block;
				position: relative;
				vertical-align: middle;
			}

			&.loading{
				.mdi{
					&:before{ 
						font-family: "dripicons-v2";
						content:"\e011";
						display: block;
						position: relative;
						animation: rotating 2s linear infinite;
					}
				}
			}
			&.added{
				background-color: $primary-button;
				.mdi{
					color: white;
					&:before{ 
						font-family: "dripicons-v2";
						content:"\53" 
					}
				}
			}
		}
		.added_to_cart.wc-forward{ display: none }
		.button-label{
			color: white;
		}
	}

	+.view-all{
		margin-bottom: 100px;
		padding-top: 30px;

		.button{
			margin: 0;
		}
	}
}

//- Product List View
.product-list{
	.product{
		margin-bottom: 100px;
		@media screen and (max-width: 640px){
			clear: both;
		}
		&__inner{
			display: flex;
			justify-content: flex-start;

			@media screen and (max-width: 640px){
				display: block;
				@include clearfix;
			}
		}
		&__image{
			width: 200px;
			position: relative;
			margin: 0;
			flex-shrink: 0;

			@media screen and (max-width: 640px){
				width: 100%;
				margin-bottom: 30px;
			}

			img{
				height    : auto;
				max-width : 100%;
				width     : 100%;
				display   : block;
				box-shadow: 0 10px 20px -3px rgba(0,0,0,.2);
			}

			.onsale{
				position: absolute;
				right: -10px;
				top: 20px;
				color: white;
				text-transform: uppercase;
				line-height: 1;
				padding: 7px 10px;
				border-radius: 3px;
				z-index: 2;
				@include em(14)
			}
		}

		&__detail{
			padding-left: 40px;
			
			@media screen and (max-width: 640px){
				padding-left: 0;
			}

			> .yith-wcqv-button{ display: none}
			
		}

		&__meta{
			.author{
				font-style: italic;
			}
		}

		&__title{
			margin: 0;
		}

		.avatar{
			display: none;
		}

		&__price{
			float: left;
			font-size: 18px;
			font-weight: 700;
			color: $accent-color;
		}
		&__action{
			float: right;

			.button, .added_to_cart.wc-forward{
				@include em(12);
				// padding        : 5px 20px;
				text-transform : uppercase;
				font-weight    : 700;
				letter-spacing : 1px;
				position       : relative;
				line-height    : 1;

				.dripicons-plus{
					position  : relative;
					display   : inline-block;
					top       : -3px;
					line-height: .7;
					transition: color .3s ease;
				}

				&.loading{
					.dripicons-plus{
						&:before{ 
							content:"\e011";
							display: block;
							position: relative;
							animation: rotating 2s linear infinite;
						}
					}
				}
				&.added{
					.dripicons-plus{
						color: #92dc34;
						&:before{ content:"\53" }
					}
				}
			}
			.yith-wcqv-button{
				// @extends .button;
				@extend .button--secondary;
			}
		}
	}
}

.added_to_cart.wc-forward{
	display: inline-block;
	color: $accent-color;
	border: 3px solid;
	border-radius: 40px;
}

@keyframes rotating{
	0%   { transform: rotate(0deg) }
	100% { transform: rotate(360deg) }
}

.mm-product-title{
	margin-bottom: 50px;
	&-wrap{
		border-bottom: 1px solid rgba(0,0,0,.05);
		box-shadow:0 1px 0 rgba(255,255,255,0.8);
		width: 100%;
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		padding-bottom: 30px;
	}
	.title-wrap{
		flex: 1;
		@media screen and (max-width: 768px){
			width: 100%;
			flex-basis: 100%;
			margin-bottom: 30px;
		}
		.product-title{
			@include em(32);
			margin: 0;
			font-weight: 700;
			display: inline-block;
		}
		.onsale{
			background-color: darken(red,5%);
			color: white;
			display: inline-block;
			vertical-align: top;
			padding: 5px 10px;
			border-radius: 3px;
			font-weight: 900;
			line-height: 1;
			font-size: 14px;
			text-transform: uppercase;
			letter-spacing: 1px;
		}
	}
	.post__share{
		strong, .label{ display: none }
		a{
			@include em(22);
			padding: 10px;
			color: $detail-color;

			&.facebook:hover{ color: $facebook }
			&.twitter:hover{ color: $twitter }
			&.google-plus:hover{ color: $google-plus }
			&.linkedin:hover{ color: $linkedin }
		}
	}
}

//- Single Product
.mm-product-single{
	background-color: white;
	padding: 50px 0 0;
	position: relative;
	z-index: 1;
	@include clearfix;
	
	.mm-top-bg{
		position: absolute;
		width: 100%;
		left: 0;
		top: 0;
		height: 100px;
		z-index: -1;
		opacity: 0;
		transition: opacity .3s ease;

		&.loaded{
			opacity: 1;

			@media screen and (max-width: 990px){
				display: none;
			}
		}
	}

	.mm-product-action{
		padding-bottom: 30px;
		margin-bottom: 30px;

		+ form{
			&.cart{
				.quantity input{ width: 100%; }
				input{
					background-color: #f5f6fa;
					border-radius: 3px;
					border: none;
				}
			}
			.button{
				margin: 20px 0 0;
				width: 100%;
			}
		}
	}

	.variations_form.cart{
		margin-bottom: 50px;
		@includ clearfix;
		.variations{
			margin-bottom: 15px;
		}
		table{
			box-shadow: none;
		}
		tr{
			background-color: transparent;
			display: block;
			margin-bottom: 10px;
		}
		td{
			display: block;
			padding: 5px 0;
		}
		.label{
			text-transform: capitalize;
			color: $heading-color;
			font-weight: 700;
		}
		select{
			display: block;
			width: 100%;
			margin-bottom: 10px;
			background-color: #f5f6fa;
			border-radius: 3px;
			border: none;
		}
		.reset_variations{
			float: right;
			font-size: 80%;
			text-decoration: underline;
			font-style: italic;
		}
	}

	.single_variation_wrap{
		input,.button{
			width: 100%;
			margin: 0;
		}
		.button{
			margin-top: 15px;
		}
	}

	.product-bookmark{
		margin: 20px 0 50px;
		text-align: center;
		.fa.fa-heart-o{
			color: red;
		}
	}

	.product_additional_info{
		table{
			box-shadow: none;
			tr{
				background-color: transparent;
				border-bottom: 1px solid rgba(0,0,0,.05);
				padding: 10px 0;
				display: block;
			}
			th,td{ 
				background-color: transparent;
				display: block;
				text-align: left;
				padding: 0;
				@include em(14);
			}
			th{ 
				color: $heading-color;
				text-transform: capitalize;
			}
			p{ margin: 0 }

		}
	}

	.mm-s-product-wdgt{
		margin-bottom: 50px;

		&-title{
			@include em(18);
			margin-bottom: 0;
		}

	}
	.product-price{
		@include em(36);
		color: $heading-color;
		line-height: 1;
		border-bottom: 1px solid rgba(0,0,0,.08);
		box-shadow: 0 1px 0 rgba(white,.8);
		padding-bottom: 20px;
		margin-bottom: 20px;
		.price{
			margin: 0;
			del{
				display: inline-block;
				vertical-align: top;
				font-weight: 700;
				color: rgba(0,0,0,.4);
				@include em(16,36);
			}
			ins{
				text-decoration: none;
				font-weight: 700;
				display: inline-block;
				vertical-align: top;
			}
		}
	}

	.woocommerce-product-rating{
		// margin-bottom: 30px;
		@include clearfix;
		.star-rating{
			font-size: 90%;
			float: left;
		}
		.woocommerce-review-link{
			float: right;
			margin: 0;
			line-height: 1;
			@include em(14);
		}
	}

	.product_author_box{
		background-color: white;
		padding: 40px 30px 20px;
		margin-top: 30px;
		border-radius: 5px;
		box-shadow: 0 5px 20px rgba(0,0,0,.05), 0 15px 30px rgba(0,0,0,.04);
		text-align: center;
		.author_avatar{
			border-radius: 50%;
			margin: 0 auto 20px;
			display: block;
		}

		.author_name{
			margin: 0;
			@include em(18);
			line-height: 1;
		}
		.author_desc{
			color: $detail-color;
		}

		.author_minfo{
			text-align: left;
			list-style: none;
			padding: 0;
			margin: 30px 0 0;

			li{
				position: relative;
				border-top: 1px solid rgba(0,0,0,.1);
				padding: 10px 0 10px 32px;

				i[class*="ti-"]{
					position: absolute;
					left: 0;
					top: 15px;
					color: $accent-color;
					@include em(18);
				}
			}
		}
	}

	.product_tags{
		.tags{
			@include em(14)
		}
	}
	
	.mm-product-details{
		padding-bottom: 100px;

		.col-md-9{
			padding-right: 50px;
			@media screen and (max-width: 990px){padding-right: 15px;}
		}
	}

	.product-images{
		.woocommerce-product-gallery__image{
			img{
				width: 100%;
				display: block;
			}
		}
		// .flex-viewport{
		// 	box-shadow: 0 10px 30px rgba(0,0,0,.1);
		// }
		.flex-control-thumbs{
			img{
				width: 100%;
				display: block;
				box-shadow: 0 10px 20px rgba(0,0,0,0.05)
			}
			.flex-active{
				box-shadow: 0
			}
		}
	}

	.product-short-description{
		margin: 50px 0;
	}
	.product-content{
		padding: 0 50px;
		@media screen and (max-width: 768px){
			padding: 0;
		}
	}

	.product-tab-header{
		margin-bottom: 50px;
	}
	
	.product-tab-icon{
		color: $accent-color;
		@include em(36)
	}
	
	.product-tab-title{
		font-weight: 700;
		margin-top: 0;
		margin-bottom: 0;
		@include em(28)
	}
	.product-tab-subtitle{
		margin-bottom: 50px;
		font-weight: normal;
		color: $detail-color;
		@include em(16);
	}

	.vendor-details{
		list-style: none;
		margin: 0;
		padding: 0;

		.seller-rating{
			display: inline-block;
		}

		li{
			position: relative;
			padding: 10px 0 10px 150px;

			&:not(:last-child){
				border-bottom: 1px solid rgba(0,0,0,.05);
			}

			.label{
				position: absolute;
				left: 0;
				top: 10px;
				font-weight: 700;
				color: $heading-color;
			}
		}
	}

	.woocommerce-tabs{
		margin-top: 50px;
		.wc-tabs{
			list-style: none;
			margin: 0 0 30px;
			padding: 0;
			border-bottom: 1px solid rgba(0,0,0,.06);
			li{
				@include em(22);
				display: inline-block;
				margin-right: 15px;
				color: $detail-color;
				padding: 20px;

				&:first-child{
					padding-left: 0;
				}

				&.active{
					color: $heading-color;
					a{
						color: inherit;
					}
				}
			}
		}
	}

	.woocommerce-Tabs-panel--reviews{
		.product-tab-title{ margin-bottom: 0; }
		.woocommerce-Reviews-title{
			margin-bottom: 50px;
			font-weight: normal;
			color: $detail-color;
			@include em(16);
		}
		.woocommerce-review__author{
			color: $heading-color
		}
	}
	
}
.mm-related-products{
	background-color: #F5F6FA;
	padding: 70px 0;

	.section-header{
		margin: 0;
	}
}


@keyframes rotate3d{
	0%{
		transform: rotateY(0deg);
	}
	50%{
		transform: rotateY(-30deg);	
	}
	70%{
		transform: rotateY(360deg);
	}
	100%{
		transform: rotateY(0);
	}
}

@keyframes rotatingDisc{
	from{
		transform: rotate(0deg);
	}
	to{
		transform: rotate(360deg);
	}

}

//- Deals related styles
.deal-end-soon{
	padding: 50px 0;
	@include clearfix;
	.product-overview{

		&__image{
			img{
				display: block;
				margin: 0 auto;
			}
		}
	}

	.price{
		margin: 20px 0;
		.sale-price{
			@include em(36);
			color: $accent-color;
			font-weight: 700;

		}
		.regular-price{
			@include em(14);
			del{
				text-decoration-color: $accent-color;
			}
			.amount{
				color: $accent-color;
			}
			color: $detail-color;
		}
	}

	.product-deals-info{
		background-color: white;
		display: table;
		border: 1px solid #e0e0e0;
		box-shadow: 0 10px 40px rgba(0,0,0,.1);
		border-radius: 3px;
		margin-bottom: 20px;
		@include clearfix;
		
		.deal-countdown,.deal-saving{
			float: left;
		}

		.label{
			@include em(13);
			color: lighten($detail-color,5%);
		}

		.deal-countdown{
			padding: 10px 20px;
			border-right: 1px solid #e0e0e0;
			@media screen and (max-width: 1200px){
				width: 100%;
				border-right: none;
				border-bottom: 1px solid #e3e3e3;
				text-align: center;
			}
			.timer{
				font-weight: 700;
				@include em(18);
				display: inline-block;
				margin-right: 5px;
				color: $heading-color;
			}
			.count-num{
				display: inline-block;
				margin-right: 10px;
			}
		}
		.deal-saving{
			display: table;
			@media screen and (max-width: 1200px){
				width: 100%;
				table-layout: fixed;
			}
			.saving{
				display: table-cell;
				padding: 10px 20px;
				text-align: center;

				&:not(:last-child){
					border-right: 1px solid #e0e0e0;
				}

				> span{
					display: block;
					
					&.value{
						font-weight: 700;
						@include em(18);
						color: $heading-color;
					}
				}
			}
		}
	}
}

.deal-countdown{
	padding-top: 10px;
	.timer{
		font-weight: 700;
		@include em(16);
		display: inline-block;
		margin-right: 2px;
		color: $heading-color;
		display: inline-block;
	}
	.label{
		@include em(14);
		text-transform: capitalize;
	}
	.count-num{
		display: inline-block;
		margin-right: 3px;
	}
}

.deal-tab-grid{
	.deal-tab-nav{
		list-style: none;
		text-align: center;
		padding-left: 0;
		margin: 50px 0;

		li{
			display: inline-block;

			&.active{
				a{
					border-color: $accent-color;
					color: $accent-color
				}
			}
		}


		a{
			border-radius: 40px;
			display: block;
			padding: 5px 20px;
			border: 3px solid transparent;
			transition: .3s ease;

		}
	}
	.deal-tab-content{
		@include clearfix;
	}
	.deal-tab-pane{
		display: none;

		&.active{
			display: block;
		}
	}
	.all-deals{
		padding: 50px 0;
		text-align: center;
	}
}

// Update Woocommerce 3.0
.woocommerce-product-gallery {
	position: relative;
	padding-right: 0;

	.woocommerce-product-gallery__wrapper{
		max-width: none;
		@include clearfix;
		a, img{
			display: block;
		}

		img{
			height: auto;
		}
	}

	.woocommerce-product-gallery__trigger {
		position: absolute;
		top: 15px;
		right: 15px;
		display: block;
		height: 2em;
		width: 2em;
		border-radius: 3px;
		z-index: 99;
		text-align: center;

		img {
			display: none!important;
		}

		&:before {
		    font: normal normal normal 1em/1 FontAwesome;
		    font-size: inherit;
		    text-rendering: auto;
		    -webkit-font-smoothing: antialiased;
		    -moz-osx-font-smoothing: grayscale;
		    display: block;
		    content: "\f00e";
		    line-height: 2;
		    text-indent: 0;
		}
	}

	
	.flex-control-thumbs{
		padding: 0;
		margin-top: 15px;
		margin-left: -15px;
		margin-right: -15px;
		@include clearfix;

		li {
			list-style: none;
		    cursor: pointer;
		    float: left;
		    padding: 15px;

		    img {
		    	opacity: .5;
		    	display: block;
		    	max-width: 100%;
			    -webkit-transition: all,ease,.2s;
			    -moz-transition: all,ease,.2s;
			    transition: all,ease,.2s
		    }

		    .flex-active, &:hover img{
		    	opacity: 1;
		    }
		}

		&:after, &:before {
			content: "";
			display: table;
		}
	}

	&.woocommerce-product-gallery--columns-2 {

		.flex-control-thumbs {

			li{
				width: 50%;
			}

			li:nth-child(2n+1) {
			    clear: both
			}
		}
	}

	&.woocommerce-product-gallery--columns-3 {

		.flex-control-thumbs {

			li {
			    width: 33.3333%;
			}

			li:nth-child(3n+1) {
			    clear: both
			} 
		} 
	}

	&.woocommerce-product-gallery--columns-4 {

		.flex-control-thumbs {

			li {
			    width: 25%;
			}

			li:nth-child(4n+1) {
			    clear: both
			} 
		} 
	}
}

.product-modern{
	margin: 0 -15px;
	.product{
		&__inner{
			box-shadow: 0 1px 2px rgba(0,0,0,.05);
			transition: .3s ease;
			background-color: white;

			&:hover{
				box-shadow: 0 10px 30px rgba(0,0,0,.09)
			}
		}

		&__detail{
			padding: 20px;
			background-color: #FAFBFD;
			position: relative;
			.product__title{
				font-weight: 400;
				margin: 0;
				@include em(16);
				// max-height: 2.5em;
				overflow: hidden;
			}
			.button.yith-wcqv-button{ display: none }
			.author{
				font-style  : italic;
				color       : $detail-color;
				@include em(14);
			}

			.product__category{
				@include em(12);
				margin-bottom: 10px;
			}
			.avatar{
				border-radius: 50%;
				position: absolute;
				box-shadow: 0 2px 6px rgba(0,0,0,.1);
				display: block;
			}
		}

		&__image{
			position: relative;
			padding: 20px;

			a{
				position: relative;
				display: block;
				&:after{
					content:" ";
					background-color: $accent-color;
					position: absolute;
					top: 0;
					left: 0;
					right: 0;
					bottom: 0;
					opacity: 0;
					transition: .3s ease;

				}
			}

			img{
				display: block;
				transition: .3s ease;
			}

			.onsale{
				position: absolute;
				top: 50px;
				right: -10px;
				white-space: nowrap;
				background-color : #4f75f2;
				color: white;
				font-size: 12px;
				padding: 1px 20px;
				border-radius: 40px;
				z-index: 1;

			}

			.yith-wcqv-button{
				background-color: transparent;
				color: white;
				padding: 0;
				box-shadow: none;
				position: absolute;
				width: 32px;
				height: 32px;
				font-size: 24px;
				top: 0;
				bottom: 0;
				right: 0;
				left: 0;
				margin: auto;
				opacity: 0;
				transition: .3s ease;

				&:before{
					content:"\e63d";
					font-family: Themify
				}
				span{
					display: none;
				}
			}

		}
		
		&__action{
			position: relative;
			width: auto;
			height: auto;
			opacity: 1;
			padding: 20px;
			border-top: 1px solid #e3e3e3;
			display: flex;
			align-items: center;

			.price_wrap{
				flex: 1;
				color: $heading-color;
				@include em(15);

				del{
					font-size: 80%;
					vertical-align: top;
					display: inline-block;
				}
				ins{
					text-decoration: none;
					font-weight: 700;
				}
			}

			.add_to_cart_button,
			.ajax_add_to_cart,
			.product_type_variable,
			.product_type_external{
				padding: 0;
				background-color: #d8dcec;
				box-shadow: none;
				position: relative;
				margin: 5px 0;
				width: 48px;
				height: 48px;
				border-radius: 50%;
				display: flex;
				align-items: center;
				justify-content: center;
				color: white;

				.button-label{
					display: none;
				}
				&.loading{
					.mdi{
						&:before{ 
							font-family: "dripicons-v2";
							content:"\e011";
							display: block;
							position: relative;
							animation: rotating 2s linear infinite;
						}
					}
				}
				&.added{
					background-color: $primary-button;
					.mdi{
						color: white;
						&:before{ 
							font-family: "dripicons-v2";
							content:"\53" 
						}
					}
				}
			}
			.added_to_cart.wc-forward{ display: none }
			.button-label{
				display: none;
			}
		}

		&:hover{
			.product__image{
				a:after{
					opacity: .7
				}
				img{
					opacity: .5
				}
				.yith-wcqv-button{
					opacity: 1;
				}
			}
			add_to_cart_button,
			.ajax_add_to_cart,
			.product_type_variable,
			.product_type_external{
				background: $primary-button;
				box-shadow: 0 5px 10px rgba($primary-button,.6);
			}
		}
		
	}
}