//- Section Title Component
.section-header {
	position       : relative;
	padding-bottom : 20px;
	margin         : 30px 0;

	&.\--has-icon{
		padding-left: 48px;
		position: relative;
	}

	.section-icon{
		@include em(36);
		color: $accent-color;
		margin-bottom: 10px;
		display: inline-block;
		position: absolute;
		left: 0;
		top: 10px;
	}

	.section-title {
		margin      : 0;
	}
	.section-subtitle {
		@include em(15);
	}
	&--left{
		text-align: left;
		&:after{
			left  : 0;
			margin : 0 auto;
		}
	}
	&--center{
		text-align: center;
		&:after{
			right  : 0;
			margin : 0 auto;
		}
	}
	&--right{
		text-align: right;
		&:after{
			left  : auto;
			right : 0;
		}
		
	}
}

//- Category Icon Component
.category-icon{
	margin: 0;
	&.kc-elm{
		float: none;
		width: auto;
	}
	
	.grid-item{
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;

		&.category-icon__title{
			padding: 10px 15px 10px 15px;
		}
		&.category-icon__cat{
			padding: 100px 15px;
		}
	
	}

	&__wrap{
		text-align: center;
		padding: 20px;
	}
	&__title, &__cat{
		flex: 1 0 auto;
		@media screen and (max-width:  1200px){ width: 33.3333%;}
		@media screen and (max-width:  768px){ width: 50%;}
		@media screen and (max-width:  480px){ width: 100%;}
	}
	
	&__cat{
		position: relative;
		overflow: hidden;
		z-index: 1;

		&:before{
			content:" ";
			background-image: inherit;
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			z-index: -1;
			transition: .3s ease-out;
		}

		&:hover{
			&:before{
				transform: scale(1.1);
			}
		}

		&:after{
			content: " ";
			background-color: inherit;
			position: absolute;
			width: 100%;
			height: 100%;
			left: 0;
			top: 0;
			z-index: -1;
		}


		&-image{
			width: 60px;
			height: 60px;
			margin: 0 auto 10px;
			z-index: 1;
			text-align: center;
			display: flex;
			align-items: center;
			justify-content: center;
			color: white;
			@include em(40);
			transition: .3s ease;
			@media screen and (max-width: 480px){
				@include em(32);
			}

			.no-flex &{
				padding-top: 15%;
				left: 0;
			}

			&:after{
				content:" ";
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 0;
				z-index: -1;
				border-radius: 50%;
				background-color: $accent-color;
				background-image: linear-gradient(45deg, $accent-color, $accent-color);
				transition: transform .3s ease;
				transform: scale(0);
			}

			img{
				max-width: 48px;
				display: block;
				position: relative;
			}
		}
		&-title{
			margin: 5px 0 0;
			line-height: 1;
			color: white;
			@include em(32);
		}
		&-count{
			color: white;
		}
	}
}

// Tokoo Separator
.tokoo-separator{
	border-style: solid;
	border-top: none;
	color: $border-color;

	&--dotted{ 
		border-style: dotted;
		border-top: none;
		border-width: 2px;
	}

	&--dashed{ 
		border-style: dashed;
		border-top: none;
	}
}

.tokoo-map{
	min-height: 400px;
}

.tokoo-contact-form{
	.row{
		margin-bottom: 30px;
	}
	input:not([type="submit"]),textarea{
		width: 100%;
		max-width: 100%;
	}
	textarea{
		height: 120px;
		width: 100%;
	}
}

.kc-elm.mailchimp-form{
	.mc4wp-form-fields{
		input[type="email"],
		input[type="text"]{
			padding: 20px;
		}
		p{
			margin: 0;
		}
	}
}

.tokoo-video-lightbox{
	a,img{
		display: block;
		margin: 0 auto;
		max-width: 100%;
	}
}

.mm-products-carousel{
	position: relative;
	margin-bottom: 100px;
	.grid-layout{ margin: 0 }
	.grid-item{
		margin-bottom: 30px;
		outline: none;
	}
	.slick-current .product__inner{
		box-shadow: 0 10px 30px rgba(0,0,0,.1);
	}
	.slick-prev,.slick-next{
		width: 100px;
		position: absolute;
		top: 0;
		bottom: 0;
		background-color: transparent;
		border: none;
		box-shadow: none;
		border-radius: 0;
		text-indent: -99999px;
		outline: none;
		z-index: 2;
	}
	.slick-prev{ left: 0 }
	.slick-next{ right: 0 }

	.slick-dots{
		position: absolute;
		list-style: none;
		margin: 0;
		padding: 0;
		text-align: center;
		width: 100%;
		left: 0;
		top: 100%;

		li{
			display: inline-block;
			margin: 5px;
			overflow: hidden;
		}

		button{
			text-indent: -999px;
			border-radius: 50%;
			width: 10px;
			box-shadow: none;
			height: 10px;
			padding: 0;
			margin: 0;
			outline: none;
			background-color: rgba(0,0,0,.1);
		}
		.slick-active button{
			background-color: $primary-button;
		}
	}
}

.mm-products-thumbnail-grid{
	.grid-item{
		a{
			display: block;
			position: relative;
			background-color: white;
			padding: 15px;
			box-shadow: 0 1px 1px rgba(0,0,0,.1);

			.intrinsic-ratio{ transition: .3s ease; }

			&:hover{
				box-shadow: 0 10px 20px rgba(0,0,0,.1);
				.intrinsic-ratio{
					transform: scale(.9);
				}
			}
		}
	}
}

.product-search-input{
	position: relative;
	margin: 50px auto;
	max-width: 100%;
	input{
		width: 100%;
		border: none;
		padding: 20px;
		border-radius: 40px;
		box-shadow: 0 1px 2px rgba(0,0,0,.1)
	}
	button{
		position: absolute;
		right: 10px;
		top: 8px;
		bottom: 8px;
	}
}

.mm-author-highlight{
	display: flex;
	flex-wrap: wrap;
	.author-info{
		margin-bottom: 10px;
		display: flex;
		align-items: center;
		margin-right: 30px;
	}
	.author-avatar{
		display: block;
		width: 100px;
		height: 100px;
		overflow: hidden;
		margin-right: 20px;
		.intrinsic-ratio{
			background-color: transparent;
		}
		img{
			border-radius: 50%;
		}
	}
	.author-name{
		margin: 0;
		@include em(22);
		line-height: 1;
	}
	.author-desc{
		display: block;
		color: $detail-color;
		@include em(15);
		margin-bottom: 5px;
	}
	.author-detail a{
		color: $primary-button;
		@include em(14);
	}
	.author-items{
		flex: 1;
		display: flex;
		text-align: right;
		justify-content: flex-start;
		margin-top: 20px;
		a{
			display: inline-block;
			width: 70px;
			height: 70px;
			border: 5px solid white;
			margin: 10px;
		}
	}
}

.mm-blog-grid{
	.style-plain{
		.post{
			.post-image{
				margin-bottom: 30px
			}
			.post-title,h2{
				margin: 0;
				@include em(18);
			}
			.post-date{
				font-style: italic;
				font-family: $decoration-font;
				color: $detail-color;
				@include em(14);
			}
			.post-excerpt{
				@include em(14)
			}
		}
	}
}

.mm-pricing-box{
	background-color: white;
	padding: 40px;
	margin-bottom: 30px;
	
	&.\--highlight{
		box-shadow: 0 10px 30px rgba(0,0,0,.07)
	}

	.pricing_header{
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		.product_name{
			margin: 0;
			line-height: 1;
			font-weight: 400;
			@include em(22);
		}
		.product_desc{
			color: #aaa
		}
		.product_price{
			text-align: right;
			flex-shrink: 0;
			.price,.unit{ display: block }
			.price{
				@include em(32);
				font-weight: 400;
				color: $heading-color;
				line-height: 1;
			}
			.unit{
				@include em(13);
				font-style: italic;
				color: #aaa;
			}
		}
	}

	.pricing_image{
		padding: 20px 0;

		img{
			display: block;
			margin: 0 auto;
		}
	}

	.pricing_features{
		list-style-type: none;
		padding: 0;
		margin-bottom:30px;
		
		li{
			display: flex;
			padding: 10px 0 0;
		}

		.feature_icon{
			padding: 10px 15px 10px 0;
			@include em(24);
			color: $primary-button;
			opacity: 0.7;
		}
		
		.feature_detail{
			flex: 1;
			border-bottom: 1px solid #eee;
			padding-bottom: 15px;
			span{
				color: #aaa;
				@include em(13);
			}
			h3{
				font-weight: 500;
				line-height: 1;
				margin: 0;
				color: rgba(0,0,0,.8);
				@include em(15);
			}
		}
	}
	.pricing_action{
		text-align: center;
		.button{
			width: 100%;
		}
	}
}

.testimonial-slider{
	@include clearfix;
	white-space: nowrap;
	overflow: hidden;
	position: relative;
	padding: 30px 0;

	.slick-dots{
		list-style: none;
		margin: 30px 0;
		padding: 0;
		text-align: center;

		li{
			display: inline-block;
			margin: 3px;
		}

		button{
			border: none;
			width: 8px;
			height: 8px;
			padding: 0;
			overflow: hidden;
			text-indent: -999px;
			border-radius:50%;
			background-color: rgba(0,0,0,.1);
		}
		.slick-active button{
			background-color: rgba(0,0,0,.4);
		}
	}

	.testimonial-slide{
		padding: 0 100px;
		text-align: center;
		display: inline-block;
		width: 100%;
		white-space: normal;
		outline: none;
		@media screen and (max-width: 1200px){ padding: 0 150px; }
		// @media screen and (max-width: 1200px){ padding: 0 100px; }
		@media screen and (max-width: 640px){ padding: 0 80px; }
		@media screen and (max-width: 480px){padding: 0}
		img{
			border-radius: 50%;
			margin-bottom: 10px;
			display: inline-block;
			width: 64px;
			height: 64px;
		}
		blockquote{
			border: none;
			padding: 0 0 40px;
			margin: 0 0 20px 0;
			font-size: 36px;
			font-weight: 700;
			font-family: $heading-font;
			font-style: normal;
			position: relative;
			line-height: 1.4;
			color: $heading-color;
			&:after{
				content:".....";
				position: absolute;
				bottom: 0;
				width: 100%;
				left: 0;
				font-size: 14px;
			}
			p{
				font-weight: inherit;
				font-size: inherit;
			}
		}

		cite{
			span{
				display: block;
				line-height: 1.2;
				&.fn{
					color: $heading-color;
					font-style: normal;
				}
				&.fn-title{ font-size: 14px }
			}
		}
	}

	.slick-prev,.slick-next{
		width: 48px;
		height: 48px;
		border-radius: 50%;
		border: none;
		color: inherit;
		background-color: white;
		box-shadow: 0 5px 20px rgba(0,0,0,.1);
		top: 0;
		bottom: 0;
		margin: auto;
		position: absolute;
		z-index: 1;
		outline: none;
		font-size: 24px;
	}
	.slick-prev{ left: 15px }
	.slick-next{ right: 15px }
}
