.tagcloud{
	a{
		font-size        : inherit!important;
		display          : inline-block;
		padding          : 5px 10px;
		background-color : $accent-color;
		color            : white!important;
		margin           : 0 10px 10px 0;
		line-height      : 1;
		position         : relative;

		&:before{
			content      :" ";
			width        : 0;
			border-width : 2px 5px;
			border-style : solid;
			border-color : rgba(0,0,0,.6) rgba(0,0,0,.6) transparent transparent;
			position     : absolute;
			left         : 0;
			top          : 100%;
		}
	}
}

.calendar_wrap{
	caption{
		color: $heading-color;
		font-weight: 700;
	}
	td,th{
		text-align: center;
		padding: 5px;
		font-weight: 700;
	}
	tfoot{
		a{
			color: white;
		}
	}
}

.widget_rss{
	.widget-title{
		a{
			display: inline-block;
			vertical-align: middle;
			margin-right: 10px;
		}
	}
	li{
		margin-bottom: 30px;
	}
	.rsswidget{
		display: block;
		font-weight: 600;
		color: $heading-color;
	}
	.rss-date{
		display: block;
		margin-bottom: 20px;
		@include em(14);
	}
	.rssSummary{
		@include em(14);
	}
	cite{
		display: block;
		text-align: right;
		@include em(14);
	}
}

.widget_archive{
	select{
		width: 100%;
	}
}

.widget_search{
	form{
		text-align: right;
		input[type="text"]{
			margin-bottom: 10px;
			width: 100%;
		}
		input[type="submit"]{
			margin-bottom: 10px;
			width: 100%;
		}
	}
}

.widget_categories{
	li{
		padding-top:5px;
		padding-bottom: 5px;
	}
	.children{
		margin-left: 10px;
		border-left: 1px solid #e3e3e3;
		li{
			padding-left: 20px;
			position: relative;
			
			&:before{
				content:" ";
				width: 10px;
				height: 1px;
				background-color: #e3e3e3;
				position: absolute;
				left: 0;
				top: 0;
				bottom: 0;
				margin: auto;
			}
		}
	}
}