.text-left{ text-align: left; }
.text-center{ text-align: center; }
.text-right{ text-align: right; }

.intrinsic-ratio{
	position: relative;
	display: block;
	background-color: #c3c3c3;

	&.ratio-1_1{
		padding-bottom: 100%;
	}
	
	img, iframe{
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		opacity: 0;
		transition: .5s ease-out;

		&.lazy-loaded{
			opacity: 1;
		}
	}
	iframe{
		opacity: 1;
		box-shadow: 0 5px 10px rgba(0,0,0,.1), 0 10px 30px rgba(0,0,0,.1);
	}
}
