.page-header{
	position: relative;
	padding: 100px 0;
	z-index: 1;
	background-color: white;
	margin-bottom: 50px;

	&--center{ text-align: center }
	&--right{ text-align: right }

	&-bg{
		position : absolute;
		width    : 100%;
		height   : 100%;
		z-index  : -1;
		left     : 0;
		top      : 0;
		overflow : hidden;

		.bg{
			position        : absolute;
			width           : 100%;
			height          : 100%;
			left            : 0;
			top             : 0;
			background-size : cover;
			background-position: center;
			// top             : -100%;
			background: url(../img/pattern-2.png);

			&[style]{
				background-size: cover!important;
			}

			// &:before{
			// 	content          :" ";
			// 	width            : 100%;
			// 	height           : 100%;
			// 	position         : absolute;
			// 	left             : 0;
			// 	top              : 0;
			// 	opacity: .4;
			// }
		}
	}

	.page-title{
		@include em(40);
		line-height : 1;
		margin      : 0 0 20px;
		
		@media screen and (max-width:480px){
			@include em(48);
		}
	}
	.page-subtitle{
		@include em(18);
		font-weight : 300;
		// color       : rgba(white,.9);
	}

	.product-search-input,
	.dokan-seller-search-form{
		width: 768px;
		max-width: 100%;
	}
}

.breadcrumbs{
	font-family: $decoration-font;
	// font-style: italic;

	a, span{
		display: inline-block;
	}
	a{
		color: $accent-color;
	}
}

