.error404{
	.site-content{
		display: flex;
		flex-direction: column;
		min-height: 100vh;

		.main-content{
			flex-grow       : 1;
			margin-bottom   : 0;
			display         : flex;
			flex-direction  : column;
			justify-content : center;
			align-items     : center;
			padding         : 100px 0;
			text-align      : center;
			background-size: cover;
			background-image: url(../img/dummy/outer-space.jpg);
			z-index: 1;
			position: relative;

			&:before{
				content:" ";
				width: 100%;
				height: 100%;
				position: absolute;
				left: 0;
				top: 0;
				z-index: -1;
				background-color: rgba(0,0,0,.5);
			}
		}
		.type-page{
			height: 100%;
		}
		.fourohfour{
			@include em(200);
			font-weight: 900;
			line-height: 1;
			color: white;
			margin-bottom: 50px;
			display: block;
			letter-spacing: -20px;
			.char-2{
				position: relative;
				top:50px;
				left:-20px;
				@media screen and (max-width: 640px){
					left: 0;
					top: .2em;
				}
			}
			@media screen and (max-width: 640px){
				@include em(70);
				letter-spacing: 0;
			}
		}
		.entry-title{
			font-weight: 300;
			color: rgba(white,.9);
			@media screen and (max-width: 640px){
				@include em(32);
			}
		}
		.widget_search{
			input{
				color: white;
			}
		}
	}
}

.no-content-wrap{
	padding: 100px 0;
	.no-content-icon{
		@include em(100);
		text-align: center;
		color: rgba(0,0,0,.2);
		margin-bottom: 50px;
	}
	.no-content-desc{
		@media screen and (min-width: 768px){
			width: 85%;
			margin: 0 auto;
			text-align: center;
		}
		p{
			font-weight: 300;
			@include em(24);
			line-height: 1.5;
		}
	}
}

.icon-grid{
  display: flex;
  flex-wrap: wrap;
  margin: 0 -10px;
}
.icon-item{
  width: 25%;
  padding: 10px;
  display: flex;
  align-items: center;
  padding: 10px;
  @media screen and (max-width:768px){
  	width: 50%;
  }
  @media screen and (max-width: 480px){
  	flex-direction: column;
  }
}
.icon-sample{
  font-size: 36px;
  width: 48px;
  display: inline-block;
  margin-right: 15px;
  @media screen and (max-width: 480px){
  	width: 100%;
  	margin-right: 0;
  	text-align: center;
  	margin-bottom: 10px;
  }
}

.contact-map{
	margin-bottom: 50px;
	box-shadow: 0 10px 30px rgba(0,0,0,.1);

	button{
		box-shadow: none;
	}
}
.tokoo-info-window h2{
	margin: 0 0 10px;
	font-size: 18px;
}
.contact-detail{
	.address,.phone{
		padding-left: 40px;
		position: relative;
		.fa{
			position: absolute;
			left: 0;
			top: 5px;
		}
	}
	.address{
		font-style: normal;
	}
}
.page-contact-form{
	input[type="text"],
	input[type="email"],
	textarea{
		width: 100%;
	}
}
