.footer-feature-list{
	padding: 30px 0;
	text-align: center;

	.feature__image{ 
		@include em(36);
		display: inline-block;
		vertical-align: middle;
		margin-right: 20px;
		line-height: 1;

		[class*="ti"],[class*="ico"]{
			line-height: 1;
			&:before{ line-height: inherit; }
		}
	}
	.feature__title{
		display: inline-block;
		vertical-align: middle;
		margin: 0;
		line-height: 1;
	}
}
.site-footer{
	background-color: white;
	a{
		color: $accent-color;
	}

	&__widget-area{
		padding: 70px 0;
		.grid-item{
			@media screen and (max-width: 768px){
				width: 100%!important;
			}
		}
	}

	.widget{
		.widget-title{margin-left: 0;}
	}

	&__colophon{
		background-color: white;
		padding: 20px 0;
		@media screen and (max-width: 480px){
			@include em(14);
		}
		.grid-item{
			@media screen and (max-width: 480px){
				width: 100%!important;
				text-align: center!important;
			}
		}
	}
}