.dokan-dashboard{
	.page-header, .type-page .entry-content{ margin-bottom: 0; }
	.main-content > .container{
		width: 100%;
		max-width: 100%;
		padding: 0;
	}
	.dokan-dash-sidebar ul.dokan-dashboard-menu li.active:after{
		left: auto;
		right: 0;
	}
	.dokan-dash-sidebar{
		padding-top: 50px;
	}
	.dokan-dashboard-content{
		padding-top: 50px;
		padding-bottom: 50px;

		> article {
			@extend .container
		}
	}
	.dashboard-widget.sells-graph{
		.chart-container{
			overflow: hidden;

			table{
				width: auto;
			}
		}
	}
	ul.subsubsub{
		font-size: inherit;
		a{
			color: inherit;
		}
	}

	.dokan-table {
		.diviader{
			display: none;
		}
	}

}

.dokan-single-store{

	.profile-frame {

		.profile-info-box {
			.profile-info-img{
				display: block;
			}
		}
		.profile-info-summery{
			display: flex;
			flex-direction: column;
			justify-content: center;
		}
	}
} 


.dokan-category-menu{
	border: none;

	h3.widget-title{
		padding: 0;
		background-color: transparent;
		border: none;
		margin-bottom: 20px;
	}
	#cat-drop-stack > ul li.parent-cat-wrap{
		background-color: transparent;
		ul{
			background-color: transparent!important;
		}
		a{
			margin: 0;
		}
	}
	#cat-drop-stack > ul li a{
		font-size: inherit;
	}
}
// .dokan-orders-content .dokan-orders-area ul.order-statuses-filter
.dokan-orders-content .dokan-orders-area ul.order-statuses-filter{
	padding-left: 0;
	color: inherit;
	font-size: inherit;
}
.dokan-orders-content .dokan-orders-area ul.order-statuses-filter li.active a{
	color: $accent-color;
}
.dokan-settings-content .dokan-settings-area .dokan-address-fields label{
	font-size: inherit;
}
.dokan-alert{
	p:first-child{ margin-top: 0;}
	p:last-child{ margin-bottom: 0;}
}

.dokan-dashboard-header{
	.entry-title{
		@include em(24);

		small{
			color: $accent-color;
		}
	}
}

.dokan-form-control{
	font-size: inherit;
}

.widget .seller-form{
	background-color: white;
	padding: 20px;
	@include clearfix;
	border-radius: 3px;
	box-shadow: 0 10px 20px rgba(0,0,0,.07);
}


input[type="submit"].dokan-btn-theme, a.dokan-btn-theme, .dokan-btn-theme{
	@extend .button;
}


.page-header .dokan-seller-search-form{
	margin: 30px auto 0;
	#search{
		width: 100%;
		border: none!important;
		padding: 20px 40px 20px 60px!important;
		border-radius: 40px!important;
		border: none!important;
		box-shadow: 0 1px 2px rgba(0,0,0,.1)!important;
		background-position: 30px center!important;
	}
} 