.post-grid,.post-masonry{
	.post{
		&[data-sticky] {
			position: relative;
			&:before{
				content: attr(data-sticky);
				position: absolute;
				left: 30px;
				top: 15px;
				background-color: $accent-color;
				color: white;
				padding: 2px 10px;
				z-index: 1;
				text-transform: uppercase;
				font-weight: 700;
				letter-spacing: 1px;
				@include em(12);
			}
		}
		&__inner{
			background-color: white;
			box-shadow: 0 1px 2px rgba(0,0,0,.05);
			transition: .3s ease;
			position: relative;
			overflow: hidden;
			width: 100%;
		}
		&:hover{
			.post__inner{
				box-shadow: 0 10px 20px rgba(0,0,0,.1);
			}
		}
		&__image{
			a,img{display: block}

			img{
				width: 100%;
				height: auto;
			}

		}
		// Post with no image
		&:not(.has-post-thumbnail) {
			.post__inner{
				display: flex;
				flex-direction: column;
				min-height: 15em;
			}
			.post__detail{
				flex: 1;
				display: flex;
				flex-direction: column
			}
			.post__header{
				display: flex;
				flex-direction: column;
				justify-content: center;
			}
		}
		&__detail{
			padding: 20px;

			@media screen and (max-width: 480px){
				padding: 15px;
			}

			.categories{
				@include em(14);
				display: block;
				color: $detail-color;
			}
		}
		&__header{
			flex: 1;
			margin-bottom: 20px;
		}
		&__title{
			margin: 0 0 10px;
			@include em(26);

			@media screen and (max-width: 480px){
				@include em(16);
			}
		}
		&__meta{
			@include em(14);
			margin: 0 -20px -20px;
			padding: 15px;
			border-top: 1px solid #f0f0f0;
			background-color: #fbfbfb;
			display: flex;
			align-items: center;
			@media screen and (max-width: 480px){
				@include em(10);
				line-height: 1.3;
			}

			.post__author{
				flex: 1
			}

			img{
				vertical-align: middle;
				display: inline-block;
				border-radius: 50%;
				margin-right: 10px;
				border: 2px solid white;
				box-shadow: 0 1px 10px rgba(0,0,0,.1);
			}

			.date{
				i{margin-right: 5px;}
			}
		}
		&__more{
			display: inline-block;
			margin-top: 20px;
			color: $accent-color;
			font-weight: 700;
		}
	}
}

.post-grid .post{
	display: flex;
	.post__image{
		max-height: 300px;
		overflow: hidden;

		img{
			object-fit: cover;
			max-height: 300px;
		}
	}
}

.post-list{
	.post{
		margin-bottom: 40px;
		background-color: white;
		padding: 30px 30px 35px;
		box-shadow: 0 5px 10px rgba(0,0,0,.05);
		transition: .3s ease;
		position: relative;
		overflow: hidden;
		@media screen and (max-width: 640px){
			padding: 15px;
		}


		&:hover{
			box-shadow: 0 10px 20px rgba(0,0,0,.1);
			
		}
		&__inner{
			display: table;
			width: 100%;
		}
		&__image, &__detail{
			display: table-cell;
			vertical-align: top;
			@media screen and (max-width: 640px){
				display: block;
			}
		}
		.categories{
			color: $detail-color;
			@include em(14);
		}
		&__image{
			width: 200px;
			@media screen and (max-width: 640px){
				width: 100%;
				text-align: center;

				img{
					width: 100%;
				}
			}
		}
		&.has-post-thumbnail{
			.post__detail{
				padding-left: 30px;
				@media screen and (max-width: 640px){
					padding-left: 0;
				}
			}
		}
		&__title{
			margin: 0 0 10px;
			line-height: 1.3;
			@include em(32);
		}
		&__meta{
			color: $detail-color;
			margin-bottom: 30px;
			position: relative;
			padding-bottom: 15px;

			.post__author{
				display: inline-block;
			}
			img{
				border-radius: 50%;
				display: inline-block;
				margin-right: 10px;
				vertical-align: middle;
				border: 3px solid white;
				box-shadow: 0 1px 10px rgba(0,0,0,.2);
			}
			span{
				margin-right: 10px;
			}
			a:hover{ color: $accent-color }
		}
		&__more{
			margin: 20px 0 0;
		}
	}
}

.gallery-slider{
	position: relative;
	.slides{
		list-style: none;
		padding-left: 0;
		margin: 0;
		white-space: nowrap;
		overflow: hidden;

		li{
			display: inline-block;
			white-space: normal;
		}
	}
	.flex-viewport{
		img{
			width: 100%;
			max-width: 100%;
			height: auto;
			display: block;
		}
	}

	.flex-direction-nav{
		list-style: none;
		margin: 0;
		padding-left: 0;

		.flex-next,.flex-prev{
			position: absolute;
			width: 40px;
			height: 50px;
			text-align: center;
			line-height: 50px;
			@include em(24);
			color: white;
			background-color: rgba(0,0,0,.5);
			top: 0;
			bottom: 0;
			margin: auto;
			z-index: 8;
		}
		.flex-prev{ left : 0 }
		.flex-next{ right : 0 }
		.flex-disabled{
			pointer-events: none;
			opacity: 0;
		}
	}
}

.posts-navigation{
	padding: 50px 0;
	@include clearfix;

	.next-link, .prev-link{
		padding: 5px 40px;
	}

	.next-link{ float: right }
	.prev-link{ float: left }
}

.single-post{
	.site-header{
		margin-bottom: 50px;

		@media screen and (max-width:990px){
			margin-bottom: 15px!important;
		}
	}

	.post{
		position         : relative;
		margin-bottom    : 50px;
		background-color: white;
		box-shadow: 0 10px 20px rgba(0,0,0,.06);
		
		@media screen and (max-width: 480px){
			margin-bottom: 30px;
			// padding-bottom: 30px;
		}

		&__image{
			// margin-bottom: 30px;

			img{
				width: 100%;
			}
		}

		.gallery-slider{
			margin: -30px -30px 30px;
		}


		.categories{
			margin-bottom: 15px;
			display: block;
			a{
				background-color: $accent-color;
				color: white;
				display: inline-block;
				padding: 2px 10px;
				border-radius: 3px;
				@include em(12);
				margin-right: 5px;
				margin-bottom: 5px;
			}
		}

		&__title{
			@include em(36);
			line-height: 1.3;
			margin: 0 0 15px;

			@media screen and (max-width: 768px){
				@include em(32);
			}
		}

		&__meta{
			margin-bottom: 50px;
			span{
				@include em(14);
				position: relative;

				i{
					display: inline-block;
					width: 32px;
					margin-right: 10px;
					text-align: center;
					color: $heading-color;
					position: absolute;
					left: 0;
					top: 5px;
				}
			}

			.post__author{
				display: inline-block;
				padding-right: 40px;
				position: relative;
				margin-right: 5px;

				&:after{
					content:" ";
					width: 30px;
					height: 1px;
					background-color: rgba(0,0,0,.4);
					position: absolute;
					right: 0;
					top: 0;
					bottom: 0;
					margin: auto;
				}
				img{
					border-radius: 50%;
					display: inline-block;
					vertical-align: middle;
					margin-right: 10px;
					border: 2px solid white;
				}
			}

			.byline{
				padding-left: 0;
				display: inline-block;
				a{
					color: $heading-color;
					&:hover{color:$accent-color;}
				}
			}

			
		}

		&__content{
			padding: 50px 50px 30px;

			@media screen and (max-width: 990px){
				padding: 15px;
			}
		}

		&__footer{
			padding: 30px 50px;
			margin-top: 50px;
			background-color: rgba(0,0,0,.02);
			border-top: 1px solid rgba(0,0,0,.05);
			@include clearfix;
			@media screen and (max-width: 640px){
				padding-left: 15px;
				padding-right: 15px;
			}
		}
		.post__categories,.post__tags{
			padding-left: 32px;
			position: relative;
			margin-bottom: 15px;
			[class*="ti"]{
				display: inline-block;
				margin-right: 10px;
				position: absolute;
				left: 0;
				top: 5px;
			}
			a:hover{
				color: $accent-color;
			}
		}

		&__share{
			position: relative;
			padding-left: 32px;
			&:before{
				content:"\e72e";
				font-family: themify;
				display: inline-block;
				margin-right: 10px;
				position: absolute;
				left: 0;
			}
			a{
				margin-right: 5px;
				margin-bottom: 5px;
				color: white;
				padding: 5px 10px;
				line-height: 1;
				display: inline-block;
				border-radius: 3px;
				&.facebook{background-color: $facebook}
				&.twitter{background-color: $twitter}
				&.google-plus{background-color: $google-plus}
				&.linkedin{background-color: $linkedin}
			}
			.fa{
				margin-right: 10px;
			}
			.label{
				font-size: 90%;
			}
			
		}

		

		&__nav{
			display: flex;
			flex-wrap: wrap;
			align-items: flex-start;
			padding: 30px 0 70px;
			text-align: center;

			&--prev,&--next{
				flex: 1;
				padding: 0 15px;
			}

			strong{
				text-transform: uppercase;
				font-size: 90%;
				letter-spacing: 1px;
			}


			h2{
				margin-top: 0;
				margin-bottom: 5px;
				line-height: 1.3;
			}
		}


		&__comment{
			margin-bottom: 100px;
		}
	}
}

.pingback{
	.comment-body{
		margin-bottom: 0;
		padding: 30px;
		font-weight: 700;

		a{
			font-weight: normal;
		}
		.edit-link{
			font-style: italic;
			color: $accent-color;
			float: right;
		}
	}
	+ .comment{
		margin-top: 50px;
	}
}
.comment{
	margin-bottom: 30px;
	border-radius: 3px;
	

	&-list,&list{
		list-style: none;
		margin: 0 0 100px;
		padding: 0;
	}
	&s-title{
		padding-left: 64px;
		position: relative;
		i{
			@include em(36);
			color: $accent-color;
			margin-bottom: 15px;
			position: absolute;
			left: 0;
			top: 10px;
		}
	}
	&s-area{
		.section-header{
			margin-top: 0;
		}
	}

	&-body{
		position         : relative;
		margin-bottom    : 30px;
		padding-bottom   : 30px;
		background-color : white;
		padding          : 20px 20px 20px 100px;
		box-shadow       : 0 5px 10px rgba(0,0,0,.04);
		border-radius    : 4px;

		@media screen and (max-width: 640px){
			padding-left: 0;
		}
	}

	&-meta{

		@media screen and (max-width: 640px){
			padding-left: 72px;
			position: relative;
			min-height: 50px;
			margin-bottom: 30px;
		}
		.comment-author{
			display: inline-block;
		}
		.avatar{
			position: absolute;
			left: 20px;
			top: 20px;
			border-radius: 50%;
			width: 50px;
			height: 50px;
			@media screen and (max-width: 640px){
				top:0;
				width: 36px;
				height: 36px;
			}
		}
		.fn{
			color: $heading-color;
			font-style: normal;
			font-weight: 700;
			line-height: 1;
			display: inline-block;
			margin-top: 5px;
			@include em(18);

			a{
				color: inherit;
			}
		}
		&data{
			color: $detail-color;
			font-size: 14px;
			font-style: italic;
			display: inline-block;
			margin-left: 10px;
		}
	}

	&-content{
		// margin-bottom: 30px;
		@media screen and (max-width: 640px){
			padding-left: 20px;
		}
	}

	.reply{
		margin-top: 30px;
		a{
			
			padding-right  : 20px;
			text-transform : uppercase;
			letter-spacing : 1px;
			display        : block;
			text-align     : right;
			color          : $heading-color;
			font-weight    : 700;
			@include em(14);


			&:after{
				background-color: $accent-color;
			}
		}
		span{ display: none }
	}

	.children {
		list-style: none;
		position: relative;
		&:before{
			content     :"\f112";
			font-family : "fontawesome";
			position    : absolute;
			left        : 10px;
			transform   : rotate(-180deg);
			color       : rgba(0,0,0,.2);
		}
	}

	&-respond{
		.section-header{
			margin-top: 0;
		}
		.comment-notes,.form-submit,.logged-in-as{
			padding: 0 15px;
			clear: both;
		}
		.form-submit{
			text-align: right;
			display: block;
		}
		.comment{
			&-form{
				margin: 0 -15px;
				label{
					color: $heading-color;
					font-weight: 700;
					display: inline-block;
					margin-bottom: 15px;
				}
				&-rating,.stars{
					padding: 0 15px;
				}
				&-rating{
					.stars{margin-top: 0;}
				}
				&-comment{
					width: 100%;
					padding: 0 15px;
					margin: 0 0 30px;

					textarea{
						width: 100%;
						padding: 20px 15px;
						display: block;
					}
				}
				&-author, &-email, &-url{
					width: 33.3333%;
					float: left;
					padding: 0 15px;
					margin: 0 0 10px;

					@media screen and (max-width: 640px){
						width: 100%;
					}

					input{
						width: 100%;
						display: block;
					}
				}
			}
		}
	}
}

.bypostauthor{
	.avatar{
		border: 2px solid $accent-color;
	}
	.comment-body{
		border-bottom-color: $accent-color;
	}
}

.comment-respond{
	background-color: white;
	padding: 30px;
	border-radius: 3px;
	box-shadow: 0 10px 30px rgba(0,0,0,.06);
	margin-bottom: 30px;

	.section-header{
		padding-bottom: 0;

		small{
			float: right;
			color: $detail-color;
			font-size: 16px;
		}
	}
}

@keyframes shakeShare{
	0%  { transform: rotateX(0) }
	15%  { transform: rotateX(40deg) }
	30% { transform: rotateX(-30deg) }
	50% { transform: rotateX(20deg) }
	60% { transform: rotateX(-15deg) }
	75% { transform: rotateX(10deg) }
	100%{ transform: rotateX(0) }
}

.entry-content,
.page-content,
.comment-content{

	.type-page &{
		margin-bottom: 50px;
	}
	
	h1{ 
		font-size: 3.5em;
		@media screen and (max-width: 480px){
			font-size: 3em;
		}
	}
	h2{ 
		font-size: 2.8em ;
		@media screen and (max-width: 480px){
			font-size: 2.5em ;
		}
	}
	h3{ 
		@include em(36) ;
		@media screen and (max-width: 480px){
			@include em(24) ;
		}
	}
	h4{ 
		@include em(30) ;
		@media screen and (max-width: 480px){
			@include em(22) ;
		}
	}
	h5{ 
		@include em(24) ;
		@media screen and (max-width: 480px){
			@include em(20) ;
		}
	}
	h6{ 
		@include em(18) ;
		@media screen and (max-width: 480px){
			@include em(18) ;
		}
	}

	.leading{
		@include em(21);
		font-family: $decoration-font;
		line-height: 1.7;
	}

	blockquote{
		@include em(24);
		color        : $heading-color;
		margin-left  : 0;
		padding-left : 30px;
		padding-top: 30px;
		min-height: 100px;
		background-image: url(../img/quote.png);
		background-repeat: no-repeat;

		p{
			font-family: inherit;
			font-size: inherit;
			font-style: inherit;
			color: inherit;
			line-height: inherit;
		}

		cite{
			display: block;
			padding: 40px 0 0;
			font-family: $root-fontFamily;
			font-style: normal;
			@include em(14, 24);

			&:before{
				content:" ";
				width: 40px;
				height: 1px;
				background-color: $border-color;
				display: inline-block;
				vertical-align: middle;
				margin-right: 10px;
			}
		}
	}
	img{
		height: auto;
	}
	.alignleft{
		margin: 30px 30px 30px 0;
		max-width: 60%;
		@media screen and (max-width: 480px){
			max-width: 100%;
			float: none;
			margin: 30px 0;
		}
	}
	.alignright{
		margin: 30px 0 30px 30px;
		max-width: 60%;
		@media screen and (max-width: 480px){
			max-width: 100%;
			float: none;
			margin: 30px 0;
		}
	}
	pre{
		word-break: break-word;
		max-width: 100%;
		overflow: auto;
	}
}

.alignleft{ float: left }
.alignright{ float: right }
.wp-caption{
	margin: 30px 0;
	img{ height: auto }
	&.alignleft{ margin-right: 30px }
	&.alignright{ margin-left: 30px }
	&.aligncenter{
		margin: 30px auto;
	}

	&-text{
		color: $detail-color;
	}
}
.gallery-caption{
	color: $detail-color;
	font-style: italic;
	@include em(14);
}

.comments-nav{
	position: relative;
	padding: 30px 0;
	margin-bottom: 30px;
	z-index: 1;

	.page-numbers{
		position: absolute;
		text-align: center;
		z-index: -1;
		width: 100%;
		left: 0;
		@media screen and (max-width: 768px){
			display: none;
		}
	}
	.comment-navigation{
		@include clearfix;

		.nav-previous,.nav-next{
			a{
				border: 2px solid $accent-color;
				border-radius: 40px;
				padding: 5px 30px;
				display: block;
			}
		}
		.nav-previous{
			float: left;
		}
		.nav-next{
			float: right;
		}
	}
}

.post-password-form{
	p:last-child{
		// background-color: white;
		position: relative;
		// padding: 20px;
	}
	input[type="submit"]{
		line-height: 1;
	}
}

.page-links{
	margin-top: 50px;
	.page-links-title{
		font-weight: 700;
		padding-left: 0;
	}
	span{
		padding: 0 10px;
		display: inline-block;
	}
	a{ color: $accent-color }
}

.wp-playlist.wp-audio-playlist{
	margin: 0;
	padding: 30px;
	border: 1px solid #efefef;
	box-shadow: 0 10px 20px rgba(0,0,0,.06);

	.wp-playlist{
		&-current-item{
			height: auto;

			img{
				max-width: 72px;
			}

			.wp-playlist-item-title{
				font-weight: 700;
				display: block;
				@include em(18);
			}
			.wp-playlist-item-album,.wp-playlist-item-artist{
				display: inline-block;
				margin-right: 15px;
				font-style: normal;
			}
		}

		&-tracks{
			.wp-playlist-item{
				padding: 15px 0;
				text-transform: capitalize;
			}
			.wp-playlist-item-length{
				top: 15px;
			}
		}
	}
}

.gallery{
	@include clearfix;
	
	&.gallery-columns-4{
		.gallery-item{
			width: 25%;
			&:nth-child(4n+1){ clear: both }
		}
	}
	&.gallery-columns-3{
		.gallery-item{
			width: 33.3333%;
			&:nth-child(3n+1){ clear: both }
		}
	}

	&-item{
		float: left;
		padding: 5px;

		img{
			width: 100%;
			max-width: 100%;
			display: block;
		}
	}

	&-caption{
		font-style: italic;
	}
}