fieldset{
	border: 1px solid rgba(0,0,0,.07);
	margin-bottom: 30px;
}

input,
select,
textarea{
	padding          : 10px 15px;
	background-color : white;
	border           : 1px solid rgba(0,0,0,.1);
	font-size        : inherit;
	font-family      : inherit;
	background-color : white;
	line-height      : normal;
	display          : inline-block;
	border-radius: 5px;
	box-shadow: inset 0 1px 2px rgba(0,0,0,.05);
	max-width: 100%;
	// border-radius: 1.5em;

	&:focus{
		outline: none;
	}
}

input[type="number"]{
	width: 100px;
}

textarea{
	padding-top: 20px;
	padding-bottom: 20px;
	min-height: 200px;
	line-height: 1.5;
}

select{
	appearance          : none;
	padding-right       : 60px;
	background-image    : url(../img/select-arrow.png);
	background-repeat   : no-repeat;
	background-position : right center;
	max-width           : 100%;
}

.select2-container--default .select2-selection--single{
	padding: 5px 0;
	height: 40px;
	border: 1px solid rgba(0,0,0,.1);

	.select2-selection__rendered{
		color: inherit;
	}
}
.select2-dropdown,.select2-search__field{
	border-color: rgba(0,0,0,.1);
}
.select2-container--default .select2-search--dropdown .select2-search__field{
	border-color: rgba(0,0,0,.1);
}
.select2-selection__arrow{
	top:5px!important;
}

.button{
	padding          : 7px 30px;
	color            : $primary-button-color;
	font-size        : inherit;
	border           : none;
	font-weight      : 600;
	display          : inline-block;
	background-color : $primary-button;
	transition       : .3s ease;
	line-height      : 1.5;
	text-decoration  : none;
	border-radius: 1.5em;
	box-shadow       : 0 2px 4px rgba(0,0,0,.1);
	cursor : pointer;

	[class*="ti-"]{
		margin-right: 10px;
		display: inline-block;
		vertical-align: middle;
		line-height: 1;
	}

	&:hover{
		background-color: $primary-button-hover;
		color: $primary-button-color;
	}

	&.no-ui{
		box-shadow: none;
		color: inherit;
		background-color: transparent;
		margin: 0;
		padding: 0;
	}

	&--secondary{
		background-color: $secondary-button;
		color: $secondary-button-color;

		&:hover{
			background-color: $secondary-button-hover;
			color: $secondary-button-color;
		}
	}

	&--special{
		border: none;
		padding: 10px 50px 10px 0;
		overflow: hidden;
		position: relative;
		text-transform: uppercase;
		letter-spacing: 1px;

		&:after{
			content:" ";
			width: 100%;
			height: 2px;
			position: absolute;
			top: 0;
			bottom: 0;
			margin: auto;
			margin-left: 10px;

		}
		&-left{
			padding: 10px 0px 10px 50px;
			&:after{
				display: none;
			}
			&:before{
				content:" ";
				width: 100%;
				height: 2px;
				position: absolute;
				top: 0;
				bottom: 0;
				margin: auto;
				margin-right: 10px;
			}
		}
	}
}

button,input[type="submit"],input[type="reset"]{
	@extend .button;
}


.form-group{
	margin-bottom: 30px;

	input,select{
		width: 100%;
	}
}