.widget{
	padding: 0 10px;
	margin-bottom: 50px;

	&-title{
		font-family: inherit;
		// font-style: italic;
		font-weight: 700!important;
		@include em(18);
		margin-bottom: 20px;
	}

	ul, ol{
		margin: 0;
		padding: 0;
		list-style: none;

		a{
			color: inherit;
			text-decoration: none;

			&:hover{
				color: $accent-color;
				// text-decoration: underline;
			}
		}
	}
}
@import "component.widgets-tokoo";
@import "component.widgets-woocommerce";
@import "component.widgets-wordpress";